import { setAttribute, setComponentProps, createComponent } from './index'


export function diff(dom, vnode, container) {
    // å¯¹æ¯”ç»“ç‚¹çš„å˜åŒ–
    const ret = diffNode(dom, vnode)
    if (container) {
        container.appendChild(ret)
    }
    return ret;
}

export function diffNode(dom, vnode) {
    let out = dom;
    if(vnode === undefined || vnode === null || typeof vnode === 'boolean') 
        vnode = '';
    if (typeof vnode === 'number') {
        vnode = String(vnode);
    }
    // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
    if (typeof vnode === 'string') {
        if (dom && dom.nodeType === 3) { //åˆ¤æ–­æ˜¯å¦æ›´æ–°
            if (dom.textContent !== vnode) {
                // æ›´æ–°æ–‡æœ¬å†…å®¹
                dom.textContent = vnode;
            }
        } else {
            out = document.createTextNode(vnode);
            if (dom && dom.parentNode) {
                dom.parentNode.replaceNode(out, dom);
            }
        }
        return out;
    }
    if (typeof vnode.tag === 'function') {
       return diffComponent(out, vnode);
    }
    // éæ–‡æœ¬domç»“ç‚¹
    if (!dom) {
        out = document.createElement(vnode.tag);
    }
    // æ¯”è¾ƒå­èŠ‚ç‚¹
    if (vnode.childrens &&
        vnode.childrens.length > 0 ||
        ( out.childNodes && 
          out.childNodes.length > 0
        )) {
           diffChildren(out, vnode.childrens);
    }
    diffAttribute(out, vnode);
    return out;
}
function diffComponent(dom, vnode) {
    let comp = dom;
    // å¦‚æœç»„ä»¶æ²¡æœ‰å˜åŒ–ï¼Œé‡æ–°è®¾ç½®props
    if (comp && comp.constructor === vnode.tag) {
        // è®¾ç½®props
        setComponentProps(comp, vnode.attrs)
        // èµ‹å€¼
        dom = comp.base;

    } else {
        // ç»„ä»¶ç±»å‹å‘ç”Ÿå˜åŒ–
        if (comp) {
            // å…ˆç§»é™¤å°±ç»„ä»¶
            unmountComponent(comp);
            comp = null;
        }
        // åˆ›å»ºæ–°ç»„å»º
        comp = createComponent(vnode.tag, vnode.attrs);
        // è®¾ç½®ç»„ä»¶å±æ€§
        setComponentProps(comp, vnode.attrs);
        // ç»™å½“å‰ç»„ä»¶æŒ‚è½½base
        dom = comp.base;
    }
    return dom;
}
function unmountComponent(comp) {
    removeNode(comp.base)
}
function removeNode(dom) {
    if (dom && dom.parentNode) {
        dom.parentNode.removeNode(dom);
    }
}
// æ¯”è¾ƒå±æ€§
function diffAttribute(dom, vnode) {
    // ä¿å­˜ä¹‹å‰domæ‰€æœ‰å±æ€§
    const oldAttrs = {};
    const newAttrs = vnode.attrs;
    // domåŸæœ‰èŠ‚ç‚¹ vnodeè™šæ‹ŸDOM
    const domAttrs = dom.attributes;
    console.log(domAttrs);
    [...domAttrs].forEach((item, index) => {
        oldAttrs[item.name] = item.value;
    })
    // æ¯”è¾ƒ  å¦‚æœåŸæ¥å±æ€§å’Œæ–°å±æ€§å¯¹æ¯” ä¸åœ¨æ–°çš„å±æ€§ä¸­, åˆ™å°†å…¶ç§»é™¤æ‰(è›‡è€…ä¸ºundefine)
    for (let key in oldAttrs) {
        if (!(key in newAttrs)) {
            setAttribute(dom, key, undefined);
        }
    }
    // æ›´æ–° class='active'
    for (let key in newAttrs) {
        if (oldAttrs[key] !== newAttrs[key]) {
            setAttribute(dom, key, newAttrs[key])
        }
    }
    
}

// æ¯”è¾ƒå­èŠ‚ç‚¹
function diffChildren(dom, vChildren) {
    const domChildren = dom.childNodes;
    const children = [];
    const keyed = {};

    // å°†æœ‰keyçš„èŠ‚ç‚¹(ç”¨å¯¹è±¡ä¿å­˜)å’Œæ²¡æœ‰keyçš„èŠ‚ç‚¹(ç”¨æ•°ç»„ä¿å­˜)åˆ†å¼€
    if (domChildren.length > 0) {
        domChildren.forEach((c) => {
            if (c.attributes && "key" in c.attributes) {
                keyed[c.attributes["key"]] = c;
            } else {
                children.push(c);
            }
        })
    }

    if (vChildren && vChildren.length > 0) {
        let min = 0;
        let count = children.length;
        [...vChildren].forEach((vChild, i) => {
            // è·å–è™šæ‹Ÿdomæ‰€æœ‰çš„key
            const key = vChild.key;
            let child;
            if (key) {
                // å¦‚æœæœ‰key, æ‰¾åˆ°å¯¹åº”keyå€¼çš„èŠ‚ç‚¹
                if (keyed[key]) {
                    child = keyed[key];
                    keyed[key] = undefined;
                }
            } else if (count > min) {
                // å¦‚æœæ²¡æœ‰key, åˆ™ä¼˜å…ˆæ‰¾ç±»å‹ç›¸åŒçš„èŠ‚ç‚¹
                for (let j = min; j < count; j++) {
                    let c = children[j];
                    if (c) {
                        child = c;
                        children[j] = undefined;
                        if (j === count - 1) count--;
                        if (j === min) min++;
                        break;
                    } 
                }
            }
            // å¯¹æ¯”
            child = diffNode(child, vChild);

            // æ›´æ–°dom
            const f = domChildren[i];

            if (child && child !== dom && dom !== f) {
                // å¦‚æœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©ºï¼Œè¯´æ˜æ­¤èŠ‚ç‚¹æ˜¯æ–°å¢çš„
                if (!f) {
                    dom.appendChild(child);
                    // å¦‚æœæ›´æ–°åçš„èŠ‚ç‚¹å’Œæ›´æ–°å‰å¯¹åº”ä½ç½®çš„ä¸‹ä¸€ä¸ªç»“ç‚¹ä¸€æ ·
                } else if (child === f.nextSibling) {
                    removeNode(f);
                    // å°†æ›´æ–°åçš„èŠ‚ç‚¹ç§»åŠ¨åˆ°æ­£ç¡®çš„ä½ç½®
                } else {
                    // æ³¨æ„insertBeforeçš„ç”¨æ³•ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯è¦æ’å…¥çš„èŠ‚ç‚¹
                    dom.insertBefore(child, f);
                }
            }

        })
    }

}

