import { renderComponent } from "./index";

/*
 * 1.å¼‚æ­¥æ›´æ–°state çŸ­æ—¶é—´å†…æŠŠå¤šä¸ªsetStateåˆå¹¶ä¸€ä¸ª(é˜Ÿåˆ—: å…ˆè¿›å…ˆå‡º)
 * 2.ä¸€æ®µæ—¶é—´ä¹‹åï¼Œå¾ªç¯æ¸…ç©ºé˜Ÿåˆ—ï¼Œæ¸²æŸ“ç»„ä»¶
 */
const setStateQueue = [];
const renderQueue = []; //ä¿å­˜å½“å‰ç»„ä»¶
function defer(fn) {
    return Promise.resolve().then(fn);
}
export function enqueueSetState(stateChange, component) {
    if (setStateQueue.length === 0) {
       defer(flush)
    }
    //  1.ä¸€æ®µæ—¶é—´å†…åˆå¹¶å¤šä¸ªsetState
    setStateQueue.push({
        stateChange,
        component
    })
    //  å¦‚æœrenderQueueé‡Œé¢æ²¡æœ‰ç»„ä»¶ï¼Œæ·»åŠ åˆ°é˜Ÿåˆ—ä¸­
    let r = renderQueue.some(item => {
        return item === component;
    })
    if (!r) {
        // è¯æ˜æ˜¯ç¬¬ä¸€æ¬¡æ·»åŠ 
        renderQueue.push(component);
    }

}

//  ä¸€æ®µæ—¶é—´ä¹‹å
function flush() {
    let item, component;
    // éå†çŠ¶æ€
    while(item = setStateQueue.shift()) {
        const {
            stateChange,
            component
        } = item;
        // ä¿å­˜ä¹‹å‰çš„çŠ¶æ€
        if (!component.prevState) {
            component.prevState = Object.assign({}, component.state);
        }
        if (typeof stateChange === 'function') {
            // æ˜¯ä¸€ä¸ªå›è°ƒå‡½æ•°
            Object.assign(component.state, stateChange(component.prevState, component.props));
        } else {
            // æ˜¯ä¸€ä¸ªå¯¹è±¡
            Object.assign(component.state, stateChange);
        }
        // èµ‹å€¼
        component.prevState = component.state;
    }
    // éå†ç»„ä»¶
    while(component = renderQueue.shift()) {
        renderComponent(component);
    }
}

