import Component from '../react/component'
import { diff, diffNode } from './diff'

const ReactDOM = {
    render
}

function render(vnode, container) {
    return diff(undefined, vnode, container);
    // return container.appendChild(_render(vnode));
}

function _render(vnode)  {
    if(vnode === undefined || vnode === null || typeof vnode === 'boolean') 
        vnode = '';
    if (typeof vnode === 'number') {
        vnode = String(vnode);
    }
    // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
    if (typeof vnode === 'string') {
        // åˆ›å»ºæ–‡æœ¬ç»“ç‚¹
        return document.createTextNode(vnode);
    }
    // å¦‚æœtagæ˜¯å‡½æ•°,åˆ™æ¸²æŸ“ç»„ä»¶
    if (typeof vnode.tag === 'function') {
        // 1.åˆ›å»ºç»„ä»¶
        const comp = createComponent(vnode.tag, vnode.attrs)
        // 2.è®¾ç½®ç»„ä»¶çš„å±æ€§
        setComponentProps(comp, vnode.attrs);
        // 3.ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
        return comp.base;
    }
    //  å¦åˆ™å°±æ˜¯ä¸€ä¸ªè™šæ‹Ÿdomå¯¹è±¡
    const { tag, attrs, childrens } = vnode;
    const dom = document.createElement(tag);

    if (attrs) {
        // æœ‰å±æ€§
        Object.keys(attrs).forEach(key => {
            const value = attrs[key];
            setAttribute(dom, key, value)
        })
    }

    // é€’å½’æ¸²æŸ“å­èŠ‚ç‚¹
    if (childrens) {
        childrens.forEach(child => render(child, dom))
    }

    return dom;
}
// è®¾ç½®å±æ€§
export function setAttribute(dom, key, value) {
    // å°†å±æ€§åclassName è½¬æˆclass
    if (key === 'className') {
        key = 'class';
    }

    // å¦‚æœæ˜¯äº‹ä»¶ onClick onBlur
    if (/on\w+/.test(key)) {
        key = key.toLowerCase();
        dom[key] = value || '';
    } else if (key === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || '';
        } else if (value && typeof value === 'object') {
            for (let k in value) {
                if (typeof value[k] === 'number') {
                    dom.style[k] = value[k] + 'px'
                } else {
                    dom.style[k] = value[k];
                }
            }
        }
    } else {
        // å…¶ä»–å±æ€§
        if (key in dom) {
            dom[key] = value || ''
        }
        if (value) {
            dom.setAttribute(key, value);
        } else {
            dom.removeAttribute(key);
        }
    }
}
// åˆ›å»ºç»„ä»¶
export function createComponent(comp, props) {
    // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ï¼Œåˆ™åˆ›å»ºå®ä¾‹è¿”å›
    let instance;
    if (comp.prototype && comp.prototype.render) {
        // ç±»
        instance = new comp(props);
    } else {
        // å‡½æ•° éœ€è¦æ„é€ ç±»,è½¬æˆç±»ç»„ä»¶ æ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†
        instance = new Component(props);
        instance.constructor = comp;
        instance.render = function() {
            return this.constructor(props);
        }
    }
    return instance;
}
// è®¾ç½®ç»„ä»¶å±æ€§
export function setComponentProps(comp, props) {
    if(!comp.base) {
        if (comp.compnentWillMount) {
            comp.compnentWillMount()
        } else if (comp.componentWillReceiveProps) {
            comp.componentWillReceiveProps()
        }
    }
    comp.props = props;
    renderComponent(comp);
}
// æ¸²æŸ“ç»„ä»¶
export function renderComponent(comp) {
    const renderer = comp.render(); //è¿”å›jsxå¯¹è±¡
    // const base = _render(renderer); //è¿”å›èŠ‚ç‚¹å¯¹è±¡
    const base = diffNode(comp.base, renderer);
    if (comp.base && comp.componentWillUpdate) {
        comp.componentWillUpdate();
    }
    if (comp.base && comp.componentDidUpdate) {
        comp.componentDidUpdate();
    } else if (comp.componentDidMount) {
        comp.componentDidMount();
    }
    // èŠ‚ç‚¹æ›¿æ¢
    // if (comp.base && comp.base.parentNode) {
    //     comp.base.parentNode.replaceChild(base, comp.base);
    // }
    comp.base = base;
}

export default ReactDOM;